import Vue from 'vue'
import App from './App.vue'
import router from './router'

import axios from 'axios'
import VueAxios from 'vue-axios'

import store from './store'

// 设置rem基准值
import 'lib-flexible/flexible'
import {
   Tabbar,
   TabbarItem,
   Search,
   Toast,
   NavBar,
   Tab,
   Tabs,
   Collapse,
   CollapseItem,
   GoodsAction,
   GoodsActionIcon,
   GoodsActionButton,
   Icon,
   Form,
   Field,
   Button,
   Lazyload,
   DropdownMenu,
   DropdownItem,
   List,
   Swipe,
   SwipeItem,
   Grid,
   GridItem,
   Sidebar,
   SidebarItem,
   Pagination,
   Cascader,
   ActionSheet,
   Notify,
   Checkbox,
   CheckboxGroup,
} from 'vant';


Vue.config.productionTip = false
Vue.use(VueAxios, axios)


// 全局注册组件
Vue
   .use(VueAxios, axios)
   .use(Tabbar)
   .use(TabbarItem)
   .use(Search)
   .use(NavBar)
   .use(Tab)
   .use(Tabs)
   .use(Collapse)
   .use(CollapseItem)
   .use(GoodsAction)
   .use(GoodsActionIcon)
   .use(GoodsActionButton)
   .use(Icon)
   .use(Form)
   .use(Field)
   .use(Button)
   .use(Lazyload)
   .use(DropdownMenu)
   .use(DropdownItem)
   .use(List)
   .use(Swipe)
   .use(SwipeItem)
   .use(Grid)
   .use(GridItem)
   .use(Sidebar)
   .use(SidebarItem)
   .use(Pagination)
   .use(Cascader)
   .use(ActionSheet)
   .use(Notify)
   .use(Checkbox)
   .use(CheckboxGroup)

axios.defaults.baseURL = 'https://apis.netstart.cn/bcomic';

// 添加请求拦截器
axios.interceptors.request.use(function (config) {
   // 在发送请求之前做些什么

   let appkey = "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=";

   // let token = localStorage.getItem('token');

   // 注册表单数据处理
   // 
   if (config.method === 'post') {
      config.data = config.data || {};
      let dataString = '';
      for (let key in config.data) {
         dataString += `${key}=${config.data[key]}&`
      }
      dataString += `appkey=${appkey}`;
      config.data = dataString;
      // 

   }

   // 请求加载提示
   Toast({
      type: 'loading',
      message: '加载中...',
      forbidClick: true,
      duration: 0,
   });

   return config;
}, function (error) {
   // 对请求错误做些什么
   // 关闭提示
   Toast.clear();
   return Promise.reject(error);
});

// 添加响应拦截器
axios.interceptors.response.use(function (response) {
   // 对响应数据做点什么
   // 关闭提示
   Toast.clear();

   return response;
}, function (error) {
   // 对响应错误做点什么
   // 关闭提示
   Toast.clear();

   return Promise.reject(error);
});

new Vue({
   router,
   store,
   render: h => h(App),
   data: {
      // 当前漫画详情的id
      nowIndetailId: '',
      // 记录所有章节id
      ListIdData: [],
      // 记录当前观看漫画的章节历史
      LookHistory: [],

      epListData: [],
      // 记录追漫漫画
      bookData: [],

      // 是否登录
      isLogin: false,
   }
}).$mount('#app')
